import React from "react";
import {
  Typography,
  List,
  ListItem,
  ListItemText,
  Stack,
  Chip,
  Avatar,
  Card,
  CardActionArea,
  CardContent
} from "@mui/material";
import HighlightedText from "./HighLightedText";
import SvgIcon, { SvgIconProps } from "@mui/material/SvgIcon";
import AccountCircleIcon from "@mui/icons-material/AccountCircle";

interface NikooHomes {
  ec_extract: string;
  flatData: FlatDetailsData;
  owners: string;
}

interface FlatDetailsData {
  tower: string;
  flat: string;
}

interface SearchResultsProps {
  results: NikooHomes[];
  queryData: string;
}

function HomeIcon(props: SvgIconProps) {
  return (
    <SvgIcon {...props}>
      <path d="M10 20v-6h4v6h5v-8h3L12 3 2 12h3v8z" />
    </SvgIcon>
  );
}
const SearchResults: React.FC<SearchResultsProps> = ({
  results,
  queryData
}) => {
  const getHighLighted = (text: string, searchQuery: string) => {
    const highlightedText = text.replace(
      new RegExp(`(${searchQuery})`, "gi"),
      "<b>$1</b>"
    );
    return highlightedText;
  };

  const trimOwnersDetails = (ownerData: string) => {
    let spaIndex = ownerData.indexOf("SPA");
    if (spaIndex !== -1) {
      ownerData = ownerData.substring(spaIndex, ownerData.length);
      let commaIndex = ownerData.indexOf(",");
      if (commaIndex !== -1) {
        ownerData = ownerData.substring(commaIndex + 1, ownerData.length);
      }
    }
    let ganIndex = ownerData.indexOf("GAN-1");
    if (ganIndex !== -1) {
      ownerData = ownerData.substring(0, ganIndex - 3);
    }
    return ownerData;
  };

  return (
    <div style={{ marginLeft: "10px" }}>
      <Typography variant="h6">Search Results</Typography>
      {results.length > 0 ? (
        <List>
          {results.map((result) => (
            <ListItem key={result.flatData.flat}>
              {/* <Stack direction="row" spacing={1}> */}
              {/* <Chip
                icon={<HomeIcon color="success" />}
                label={result.flatData.flat}
              />
              <Chip
                icon={<AccountCircleIcon />}
                label={trimOwnersDetails(result.owners)}
              /> */}

              {/* <ListItemText
                primary={`Flat:${result.flatData.flat}`}
                secondary={`${getHighLighted(result.owners, queryData)}`}
              /> */}
              <br />
              {/* <HighlightedText
                text={trimOwnersDetails(result.owners)}
                searchQuery={queryData}
              /> */}
              <br />

              <Card sx={{ maxWidth: 345 }}>
                <CardActionArea>
                  <CardContent>
                    <Typography gutterBottom variant="h5" component="div">
                      <Chip
                        icon={<HomeIcon color="success" />}
                        label={result.flatData.flat}
                      />
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                      <HighlightedText
                        text={trimOwnersDetails(result.owners)}
                        searchQuery={queryData}
                      />
                    </Typography>
                  </CardContent>
                </CardActionArea>
              </Card>
            </ListItem>
          ))}
        </List>
      ) : (
        <Typography>No results found.</Typography>
      )}
    </div>
  );
};

export default SearchResults;
