import { TextField, Button, IconButton } from "@mui/material";
import React, { useState } from "react";

import { isInsideGeofence } from "./data.service";
import NotificationComponent from "./NotificationComponent";
import MicOutlinedIcon from "@mui/icons-material/MicOutlined";
import MicOffOutlinedIcon from "@mui/icons-material/MicOffOutlined";

interface SearchFormProps {
  searchData: (searchTerm: string) => void;
}

const SpeechRecognition =
  (window as any).SpeechRecognition || (window as any).webkitSpeechRecognition;

const SearchForm: React.FC<SearchFormProps> = ({ searchData }) => {
  const [searchTerm, setSearchTerm] = useState("");
  const [showAlert, setShowAlert] = React.useState(false);
  const [geoFenceFlag, setGeoFenceFlag] = React.useState(false);
  const [recognitionState, setRecognitionState] = React.useState<any>();
  const [micState, setMicState] = React.useState<string>("Start Voice Search");
  const [voiceSearchSupportFlag, setVoiceSearchSupportFlag] =
    React.useState(true);

  const onResult = (event: any) => {
    console.log("onResult:::");
    if (recognitionState) recognitionState.stop();
    for (const res of event.results) {
      const data = res[0].transcript;
      console.log("onResult::data:" + data);
      if (data) {
        setSearchTerm(data);
        searchData(data);
        setMicState("Start Voice Search");
      }
    }
  };

  const onSpeechError = (event: any) => {
    for (const res of event.error) {
      console.log("onSpeechError:" + res);
    }
  };

  const handleSpeechRecognition = () => {
    if (SpeechRecognition) {
      if (micState === "Stop Voice Search") {
        setMicState("Start Voice Search");
        if (recognitionState) {
          recognitionState.stop();
        }
      } else {
        setMicState("Stop Voice Search");
        if (recognitionState) {
          recognitionState.stop();
        }
        const recognition = new SpeechRecognition();
        recognition.start();
        recognition.continuous = true;
        recognition.interimResults = true;
        recognition.addEventListener("result", onResult);
        recognition.addEventListener("error", onSpeechError);
        setRecognitionState(recognition);
        // Speech Recognition API is available
        // Perform your logic here
      }
    } else {
      console.log("else  loop");
      setVoiceSearchSupportFlag(false);
      // Speech Recognition API is not supported
      // Handle the lack of support or provide alternative functionality
    }
  };

  const handleSearch = (e: React.FormEvent) => {
    e.preventDefault();
    if (searchTerm && searchTerm.trim().length > 0) {
      searchData(searchTerm);
    } else {
      console.log("no search string");
    }
  };

  const checkGeoFence = () => {
    console.log("checkGeoFence :: " + searchTerm);
    if (searchTerm && searchTerm.trim().length > 0) {
      navigator.permissions
        .query({ name: "geolocation" })
        .then(function (result) {
          // Will return ['granted', 'prompt', 'denied']
          if (result.state === "granted") {
            navigator.geolocation.getCurrentPosition(
              successCallback,
              errorCallback
            );
          }
          if (result.state === "denied") {
            alert(
              ` please grant permission to access your device's location. Without it, the search feature will be unable to retrieve relevant details.`
            );
          }
          console.log(result.state);
        });
    }
  };

  const successCallback = (position: GeolocationPosition) => {
    const latitude = position.coords.latitude;
    const longitude = position.coords.longitude;
    if (isInsideGeofence(latitude, longitude)) {
      console.log("Inside geofence.");
      setShowAlert(true);
      setGeoFenceFlag(true);
      if (searchTerm && searchTerm.trim().length > 0) {
        searchData(searchTerm);
      }
    } else {
      console.log("Outside geofence.");
      setShowAlert(true);
      setGeoFenceFlag(false);
    }
  };

  const errorCallback = (error: GeolocationPositionError) => {
    console.error("Error accessing geolocation:", error.message);
  };

  return (
    <>
      <TextField
        value={searchTerm}
        required
        style={{ width: "60%", marginLeft: "10px" }}
        fullWidth
        label="Search by name..."
        id="fullWidth"
        onChange={(e) => setSearchTerm(e.target.value)}
      />
      <Button
        onClick={handleSearch}
        type="button"
        variant="outlined"
        color="primary"
        style={{
          marginLeft: "10px",
          width: "20%",
          height: "54px"
        }}
      >
        Search
      </Button>
      <br />
      <br />
      <span
        style={{
          marginLeft: "2%"
        }}
      >
        Or
      </span>

      <Button
        onClick={handleSpeechRecognition}
        variant="outlined"
        startIcon={<MicOutlinedIcon />}
        disabled={!voiceSearchSupportFlag}
        style={{
          marginLeft: "11%",
          width: "80%",
          height: "50px"
        }}
      >
        {micState}
      </Button>

      <NotificationComponent
        show={showAlert}
        flag={geoFenceFlag}
        message={
          geoFenceFlag
            ? "Inside NIKOO HOMES-II premises"
            : "Accessing the restricted data outside the NIKOO HOMES-II premises is not permitted."
        }
      ></NotificationComponent>

      <NotificationComponent
        show={true}
        flag={voiceSearchSupportFlag}
        message={
          voiceSearchSupportFlag
            ? "Voice Supported"
            : "Voice not supported by device browser"
        }
      ></NotificationComponent>
    </>
  );
};

export default SearchForm;
